@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&family=Varela+Round');

@layer base {
  .background {
    @apply container m-auto max-w-3xl bg-navy p-8 text-white;
  }

  .page {
    @apply m-auto w-full bg-navy-dark text-white;
  }

  body {
    font-family: 'Quicksand', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }

  h1 {
    @apply text-4xl font-bold mb-2;
  }

  h2 {
    @apply text-3xl font-bold mb-1;
  }

  h3 {
    @apply text-xl font-medium;
  }

  h4 {
    @apply text-lg font-medium;
  }
}

@layer component {
  .skill-card {
    @apply rounded-xl bg-navy-shade text-white py-2;
  }

  .attribute-card {
    @apply mx-auto flex max-w-sm items-center space-x-4 rounded-xl px-4 py-2;
  }

  .photo-template-card {
    @apply h-96 w-64 rounded-3xl border-opacity-80 bg-slate-500;
  }

  .photo-card {
    @apply m-auto rounded-3xl w-full h-full object-cover;
  }

  .label-text {
    @apply block font-medium text-gray-200;
  }

  .input-text {
    @apply border rounded-lg block w-full p-2.5 bg-slate-500 border-gray-800 placeholder-gray-400 text-white focus:ring-blue-500 focus:border-blue-500
  }

  .biome-chip {
     @apply m-2 px-4 rounded-xl bg-gray-600 font-medium;
  }
}
